*, *::before, *::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--color-void);
  color: var(--color-text);
  font-family: var(--font-body);
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
  overflow-x: hidden;
}
::selection {
  background: rgba(0,229,255,.20);
  color: var(--color-accent);
}
::-webkit-scrollbar {
  width: 4px;
}
::-webkit-scrollbar-track {
  background: var(--color-void);
}
::-webkit-scrollbar-thumb {
  background: var(--color-border);
  border-radius: 2px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--color-muted);
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
.grid-bg {
  background-image: linear-gradient(rgba(0,229,255,.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0,229,255,.03) 1px, transparent 1px);
  background-size: 40px 40px;
}
.accent-glow {
  box-shadow: 0 0 20px rgba(0,229,255,.15), 0 0 40px rgba(0,229,255,.05);
}
.shimmer-bg {
  background: linear-gradient(
    90deg,
    var(--color-surface) 0%,
    var(--color-border)  50%,
    var(--color-surface) 100%
  );
  background-size: 200% 100%;
  animation: var(--animate-shimmer);
}
.countdown-digit {
  font-family: var(--font-mono);
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--color-accent);
  background: rgba(0,229,255,.06);
  border: 1px solid rgba(0,229,255,.15);
  border-radius: 6px;
  padding: 2px 6px;
  display: inline-block;
  min-width: 36px;
  text-align: center;
}
.btn-primary {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-accent);
  color: var(--color-void);
  font-family: var(--font-display);
  font-weight: 600;
  border-radius: 0.75rem;
  padding: 0.75rem 1.5rem;
  transition: background-color .2s, transform .1s;
  cursor: pointer;
  border: none;
}
.btn-primary:hover {
  background-color: var(--color-accent-dim);
}
.btn-primary:active {
  transform: scale(.97);
}
.btn-primary:disabled {
  opacity: .4;
  cursor: not-allowed;
  transform: none;
}
.input-field {
  width: 100%;
  background-color: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 0.75rem;
  padding: 0.75rem 1rem;
  color: var(--color-text-bright);
  font-family: var(--font-body);
  transition: border-color .2s, box-shadow .2s;
  outline: none;
}
.input-field::placeholder {
  color: var(--color-muted);
}
.input-field:focus {
  border-color: rgba(0,229,255,.6);
  box-shadow: 0 0 0 3px rgba(0,229,255,.08);
}
